import { showToast } from '../utils/toast';
const TUTORIAL_STEPS = [
    {
        title: '–í—ñ—Ç–∞—î–º–æ –≤ –≥—Ä—ñ!',
        text: '–¶–µ –≥—Ä–∞ –¥–ª—è –≤–∏–≤—á–µ–Ω–Ω—è —Ä–∏–∑–∏–∫-–º–µ–Ω–µ–¥–∂–º–µ–Ω—Ç—É –≤ —Ç—Ä–µ–π–¥–∏–Ω–≥—É. –í–∏ –±—É–¥–µ—Ç–µ —Å–∏–º—É–ª—é–≤–∞—Ç–∏ —Ç–æ—Ä–≥–æ–≤—ñ –ø–æ–∑–∏—Ü—ñ—ó —Ç–∞ –¥–∏–≤–∏—Ç–∏—Å—è –Ω–∞ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∏.'
    },
    {
        title: '–¢–æ—Ä–≥–æ–≤—ñ –ø–æ–∑–∏—Ü—ñ—ó',
        text: '–ö–æ–∂–Ω–∞ –∫–ª—ñ—Ç–∏–Ω–∫–∞ –ø—Ä–µ–¥—Å—Ç–∞–≤–ª—è—î —Ç–æ—Ä–≥–æ–≤—É –ø–æ–∑–∏—Ü—ñ—é. –ö–ª—ñ–∫–∞–π—Ç–µ –ø–æ –Ω–∏—Ö, —â–æ–± –ø–æ–∑–Ω–∞—á–∏—Ç–∏ —è–∫ —Ç–µ–π–∫ (–ø—Ä–∏–±—É—Ç–æ–∫) –∞–±–æ —Å—Ç–æ–ø (–∑–±–∏—Ç–æ–∫).',
        target: '#positions-grid'
    },
    {
        title: '–°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞',
        text: '–¢—É—Ç –≤—ñ–¥–æ–±—Ä–∞–∂–∞—î—Ç—å—Å—è –≤–∞—à–∞ —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞: –∫—ñ–ª—å–∫—ñ—Å—Ç—å —Ç–µ–π–∫—ñ–≤, —Å—Ç–æ–ø—ñ–≤, –∑–∞–≥–∞–ª—å–Ω–∏–π –ø—Ä–∏–±—É—Ç–æ–∫ —Ç–∞ win-rate.',
        target: '.stats-panel'
    },
    {
        title: '–ù–∞–ª–∞—à—Ç—É–≤–∞–Ω–Ω—è',
        text: '–¢—É—Ç –≤–∏ –º–æ–∂–µ—Ç–µ –Ω–∞–ª–∞—à—Ç—É–≤–∞—Ç–∏ —Ä–∏–∑–∏–∫ –Ω–∞ –ø–æ–∑–∏—Ü—ñ—é —Ç–∞ —Å–ø—ñ–≤–≤—ñ–¥–Ω–æ—à–µ–Ω–Ω—è –ø—Ä–∏–±—É—Ç–∫—É (RR).',
        target: '.settings-section'
    },
    {
        title: '–ì—Ä–∞–¥–∞—Ü—ñ—ó –ø—Ä–∏–±—É—Ç–∫—É',
        text: '–°–∏—Å—Ç–µ–º–∞ –ø–æ–∫–∞–∑—É—î –≤–∞—à –ø—Ä–æ–≥—Ä–µ—Å —á–µ—Ä–µ–∑ —Ä—ñ–∑–Ω—ñ —Ä—ñ–≤–Ω—ñ –ø—Ä–∏–±—É—Ç–∫–æ–≤–æ—Å—Ç—ñ.',
        target: '.profit-grades-section'
    },
    {
        title: '–ì–æ—Ç–æ–≤–æ!',
        text: '–¢–µ–ø–µ—Ä –≤–∏ –∑–Ω–∞—î—Ç–µ –æ—Å–Ω–æ–≤–∏. –ü–æ—á–Ω—ñ—Ç—å –∫–ª—ñ–∫–∞—Ç–∏ –ø–æ –ø–æ–∑–∏—Ü—ñ—è—Ö —Ç–∞ —Å–ø–æ—Å—Ç–µ—Ä—ñ–≥–∞–π—Ç–µ –∑–∞ —Å–≤–æ—ó–º–∏ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞–º–∏!'
    }
];
class TutorialManager {
    constructor() {
        this.currentStep = 0;
    }
    start() {
        this.currentStep = 0;
        this.createOverlay();
        this.showStep();
    }
    createOverlay() {
        this.overlay = document.createElement('div');
        this.overlay.className = 'tutorial-overlay';
        this.overlay.style.cssText = `
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.7);
      z-index: 9999;
      display: flex;
      align-items: center;
      justify-content: center;
    `;
        this.modal = document.createElement('div');
        this.modal.className = 'tutorial-modal';
        this.modal.style.cssText = `
      background: var(--surface-color);
      padding: 2rem;
      border-radius: 16px;
      max-width: 500px;
      margin: 1rem;
      text-align: center;
      border: 1px solid var(--border-color);
      box-shadow: var(--shadow-lg);
    `;
        this.overlay.appendChild(this.modal);
        document.body.appendChild(this.overlay);
    }
    showStep() {
        const step = TUTORIAL_STEPS[this.currentStep];
        if (!step || !this.modal)
            return;
        // Highlight target element
        this.highlightTarget(step.target);
        this.modal.innerHTML = `
      <h3 style="margin-bottom: 1rem; font-size: 1.25rem; font-weight: 700;">${step.title}</h3>
      <p style="margin-bottom: 2rem; color: var(--text-muted);">${step.text}</p>
      <div style="display: flex; gap: 1rem; justify-content: center;">
        ${this.currentStep > 0 ? '<button id="tutorial-prev" class="btn btn-secondary">–ù–∞–∑–∞–¥</button>' : ''}
        <button id="tutorial-next" class="btn btn-primary">
          ${this.currentStep === TUTORIAL_STEPS.length - 1 ? '–ó–∞–≤–µ—Ä—à–∏—Ç–∏' : '–î–∞–ª—ñ'}
        </button>
        <button id="tutorial-skip" class="btn btn-outline">–ü—Ä–æ–ø—É—Å—Ç–∏—Ç–∏</button>
      </div>
    `;
        // Add event listeners
        this.modal.querySelector('#tutorial-next')?.addEventListener('click', () => {
            if (this.currentStep === TUTORIAL_STEPS.length - 1) {
                this.finish();
            }
            else {
                this.next();
            }
        });
        this.modal.querySelector('#tutorial-prev')?.addEventListener('click', () => {
            this.prev();
        });
        this.modal.querySelector('#tutorial-skip')?.addEventListener('click', () => {
            this.finish();
        });
        // Execute step action
        if (step.action) {
            step.action();
        }
    }
    highlightTarget(selector) {
        // Remove previous highlights
        document.querySelectorAll('.tutorial-highlight').forEach(el => {
            el.classList.remove('tutorial-highlight');
        });
        if (selector) {
            const target = document.querySelector(selector);
            if (target) {
                target.classList.add('tutorial-highlight');
                // Add highlight CSS if not exists
                if (!document.querySelector('#tutorial-highlight-style')) {
                    const style = document.createElement('style');
                    style.id = 'tutorial-highlight-style';
                    style.textContent = `
            .tutorial-highlight {
              position: relative;
              z-index: 10000;
              box-shadow: 0 0 0 4px var(--primary-color), 0 0 0 8px rgba(139, 92, 246, 0.3) !important;
              border-radius: 8px !important;
            }
          `;
                    document.head.appendChild(style);
                }
            }
        }
    }
    next() {
        this.currentStep++;
        this.showStep();
    }
    prev() {
        this.currentStep--;
        this.showStep();
    }
    finish() {
        if (this.overlay) {
            document.body.removeChild(this.overlay);
        }
        // Remove highlights
        document.querySelectorAll('.tutorial-highlight').forEach(el => {
            el.classList.remove('tutorial-highlight');
        });
        // Remove highlight style
        const style = document.querySelector('#tutorial-highlight-style');
        if (style) {
            style.remove();
        }
        showToast('–¢—É—Ç–æ—Ä—ñ–∞–ª –∑–∞–≤–µ—Ä—à–µ–Ω–æ!', 'success');
    }
}
export const tutorialManager = new TutorialManager();
